button, input, select, textarea {
  font-family: inherit;
  font-size: 100%;
  width : 150px;
  padding: 0;
  margin: 0;
  box-sizing: border-box;
}

table {
  font-family: arial, sans-serif;
  border-collapse: collapse;
  width: 100%;
}

td, th {
  border: 1px solid #dddddd;
  text-align: left;
  padding: 8px;
}

tr:nth-child(even) {
  background-color: rgba(221, 221, 221, 0.2);
}

tr {
  height: 1px;
}

h2 {
  text-align: center;
}

.enter-round {
  margin: auto;
  width: 50%;
  border: 3px;
  padding: 10px;
}

body {
  background: rgb(9,2,125);
  background: linear-gradient(90deg, rgba(9,2,125,1) 0%, rgba(104,16,198,1) 91%);
  color: rgba(150, 150, 200, 1);
}

.input-div {
  margin-bottom: 15px;
  display: flex;
  justify-content: center;
  align-items: center;
}
.inner-div {
  margin: auto;
  width: 40%;
  padding: 10px;
}
label {
  display: inline-block;
  width: 110px;

}
.noentries, .statlist {
  color: rgba(255, 255, 255, 1);
}
input {
  padding: 5px 10px;
}

/* Style the tab */
.tab {
  overflow: hidden;
  border: 1px solid #ccc;
  background-color: #f1f1f1;
}

/* Style the buttons that are used to open the tab content */
.tab button {
  background-color: inherit;
  float: left;
  border: none;
  outline: none;
  cursor: pointer;
  padding: 14px 16px;
  transition: 0.3s;
}

/* Change background color of buttons on hover */
.tab button:hover {
  background-color: #ddd;
}

/* Create an active/current tablink class */
.tab button.active {
  background-color: #ccc;
}

/* Style the tab content */
.tabcontent {
  display: none;
  padding: 6px 12px;
  border: 1px solid #ccc;
  border-top: none;
}

.tabcenter {
  display: flex;
  justify-content: center;
  align-items: center;
}
